<div className="flex items-center gap-4">
  {/* 1. 이름 풀네임 및 권한 텍스트 (관리자 페이지와 동일한 레이아웃) */}
  <div className="hidden md:block text-right">
    <p className="text-sm font-bold leading-none">{userName}님</p>
    <p className="text-[10px] text-muted-foreground uppercase tracking-widest mt-1 font-black italic">
      Standard User
    </p>
  </div>

  {/* 2. 아이콘 버튼들 */}
  <div className="flex items-center gap-1">
    <Link href="/dashboard/chat">
      <Button variant="ghost" size="icon" className="rounded-xl">
        <MessageSquare className="h-5 w-5" />
      </Button>
    </Link>
    <Link href="/dashboard/settings">
      <Button variant="ghost" size="icon" className="rounded-xl">
        <Settings className="h-5 w-5" />
      </Button>
    </Link>
  </div>

  {/* 3. 아바타 (첫 글자 추출) */}
  <Avatar className="h-9 w-9 border-2 border-primary/20 shadow-sm">
    <AvatarFallback className="bg-primary text-primary-foreground text-xs font-black uppercase">
      {userName?.charAt(0)}
    </AvatarFallback>
  </Avatar>

  {/* 4. 로그아웃 버튼 */}
  <Button 
    variant="ghost" 
    size="icon" 
    onClick={handleLogout} 
    className="rounded-xl hover:bg-destructive/10 hover:text-destructive transition-all"
  >
    <LogOut className="h-5 w-5" />
  </Button>
</div>